.node-preview-container {
  background: #f5f5f2;
  border-top: 1px solid #aaa;
  padding: 0;
  font-family: Source Sans Pro, Lucida Grande, Verdana, sans-serif;
  font-size: 13px;
  font-weight: normal;
  inset: auto 0 0;
}

body.toolbar-hidden .node-preview-container {
  display: none;
}

.node-preview-container .node-preview-form-select {
  flex-flow: wrap;
  justify-content: center;
  display: flex;
}

.node-preview-container a {
  color: #565656;
  padding: 9px 18px;
  text-decoration: none;
  display: block;
}

.node-preview-container a:hover {
  color: #000;
  background: #fff;
}

.node-preview-container a.is-active {
  color: #000;
  font-weight: bold;
}

.node-preview-container .form-element--edit-view-mode {
  display: none;
}

